import {useEffect, useContext, useRef, useState} from 'react';
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../../application/middlewares/auth';
import {getIsLogged, getToken} from '../../application/selectors/auth';
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import { post } from '../../application/middlewares';
import { loginSuccess, setToken } from '../../application/actions/auth';
import logo1 from '../image/logo1.svg'
import fon_gamysh from '../image/fon_gamysh.svg'
import Lang1 from '../Language/Lang1';
import { LangContext } from "../Language/LangContext";
import useOnClickOutside from '../../components/useOnClickOutside'





function ForgotPassword(){
    const { lang, dispatchLang } = useContext(LangContext);
    
    const {  control, handleSubmit, formState: { errors }, setError} = useForm({
        resolver: yupResolver(schema(lang))
    });
    const dispatch = useDispatch();
    const history = useHistory();
    const [input993, setInput993] = useState(false)

    const ref = useRef()
    useOnClickOutside(ref, () =>setInput993(false));

    // const categories = useSelector(getCategory);
    // const user = useSelector(getUser);
    const token = useSelector(getToken);
    // const dispatch = useDispatch();
    // const isLogged = useSelector(getIsLogged);
    // const history = useHistory();
    // useEffect(() => {
    //     if(isLogged === false){
    //         dispatch(LoadUser({history}));
    //     }
    //     // eslint-disable-next-line
    // }, [dispatch, isLogged]);
    // console.log(token)
    const onSubmit = (data) => {
        console.log(data)

        dispatch(post({
            url:'user/forgot-password',
            token: token,
            data,
            action:(response)=>{
                if(response.success){
                    // console.log("gelen login",response)
                    dispatch(setToken(response.data.token));
                    // console.log("token ", response.data.token)
                    const payload = {"token":response.data.token}
                    // console.log(payload)
                    history.push('/auth/change_password')
                    
                    
                }else{
                    console.log("forgot Errors ",response);
                    // console.log(response?.message?.data?.error?.phone)
                    setError('phone', { type:'manual', message:`${lang === 'ru' ? "Телефон неправильный" : "Telefon nomeryňyz ýalňyş"}`});
                }
            },

        }))
    }
    return(
        <div className="flex justify-center items-center h-full w-full fon_gamysh"
        style={{ backgroundImage: `url(/gamysh_fon.png)` }}
        >
        <div className="flex max-w-sm mx-auto overflow-hidden rounded-lg shadow-lg dark:bg-gray-800 lg:w-80">
        <form onSubmit={handleSubmit(onSubmit)}
        className="w-full px-6 py-8 md:px-8">
            <Link to='/' className='flex justify-center w-full my-5'>
            <img src='https://gamysh.com/uploads/images/logo1.svg' className='auth_img' />
            </Link>
            <h2 className="text-2xl font-semibold text-center text-gray-700 dark:text-white my-5">{Lang1[lang].forgot}</h2>
            {/* <p className="text-xl text-center text-gray-600 dark:text-gray-200">Welcome back!</p> */}
            <div ref={ref} className="mt-4 w-full relative"
             onClick={() =>
                setInput993(!input993)
            }
            >
            <Controller
                    control={control}
                    name="phone"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <>
                            {

                                value || input993
                                ?
                                <div className="input_993">
                                <p className='font-semibold text-gray-500'>+993</p>
                                </div>
                                :
                                null
                            }
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                            type="tel"  size="regular" color="blue"
                            style={{paddingLeft:"3.5rem"}}

                            outline={true} placeholder={Lang1[lang].phone} error={errors.phone?.message} />
                            </>
                    )}}
                />
            </div>
           

            <div className="mt-8 w-full flex justify-end items-center">
            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
            >
                {Lang1[lang].send}
            </Button>
            </div>
        </form>
    </div>
    </div>
    )
}

const schema =(lang) => Yup.object().shape({
    phone:Yup.string().min(8, Lang1[lang].phone_min).max(8, Lang1[lang].phone_max)
      .required(
        //   "Telefon nomer bolmaly"
        Lang1[lang].required
          )
      .matches(
        /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/,
      Lang1[lang].only_number
    ),
  });

export default ForgotPassword;